.ferry-times {
  overflow: scroll;
  // flex-grow: 1;
  text-align: center;
  /* for Firefox */
  min-height: 0;
  background-color: var(--color-3);
  padding-top: 0.5em;

  :first-child {
    background-color: yellow;
    font-size: 2em;
  }
  :nth-child(2) {
    font-size: 1.6em;
  }
  :nth-child(3) {
    font-size: 1.2em;
  }
}
